.hiring {
  padding: 153px 0;
  background: linear-gradient(
    268.52deg,
    #e3e7f6 -52.27%,
    rgba(227, 231, 246, 0) 138.23%
  );
  // .hiring__container

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include small-tablet {
      justify-content: center;
      flex-direction: column;
    }
  }

  // .hiring__content

  &__content {
    max-width: 335px;
    @include small-tablet {
      text-align: center;
    }
  }

  // .hiring__title

  &__title {
    margin-bottom: 16px;
  }

  // .hiring__text

  &__text {
    margin-bottom: 40px;
  }

  // .hiring__btn

  &__btn {
    position: relative;
    &::after {
      content: url(../img/about/next.svg);
      width: 94px;
      height: 49px;
      position: absolute;
      bottom: -76px;
      right: 34px;
    }
  }

  // .hiring__img

  &__img {
    @include small-tablet {
      margin-top: 100px;
    }
  }
}
