.card {
  padding: 50px 0;

  // .card__content

  &__content {
    display: grid;
    align-self: center;
    justify-self: center;
    gap: 32px;
    grid-template-columns: repeat(12, 1fr);
    @include small-tablet {
      display: flex;
      align-items: center;
      flex-direction: column;
    }
  }

  // .card__col

  &__col {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    grid-column: 4 span;
    background: #ffffff;
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07),
      0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
    padding: 53px 11px 0 11px;
    @include small-tablet {
      max-width: 316px;
      margin-bottom: 32px;
    }
  }

  // .card__num

  &__num {
    font-weight: 700;
    font-size: 56px;
    line-height: 62px;
    color: var(--blue);
    margin-bottom: 18px;
  }

  // .card__text

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 22px;
    line-height: 33px;
    color: var(--grayish-blue);
    margin-bottom: 12px;
  }

  // .card__img

  &__img {
    margin-bottom: -4px;
  }
}
