.tools {
  padding-top: 290px;
  background: linear-gradient(
    247.11deg,
    #e3e7f6 3.56%,
    rgba(227, 231, 246, 0) 115.17%
  );
  @include tablet {
    padding-top: 120px;
  }
  // .tools__container

  &__container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include tablet {
      align-items: center;
      flex-direction: column;
      justify-content: center;
    }
  }

  // .tools__content

  &__content {
    max-width: 467px;
    @include tablet {
      text-align: center;
    }
  }

  // .tools__title

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 43px;
    line-height: 62px;
    color: var(--blue);
    margin-bottom: 32px;
    @include mobile {
      font-size: 27px;
      line-height: 43px;
    }
  }

  // .tools__text

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 22px;
    line-height: 33px;
    color: var(--grayish-blue);
    @include mobile {
      font-size: 20px;
    }
  }

  // .tools__img

  &__img {
    margin-bottom: -3px;
  }
}
