.dreamers {
  padding: 270px 0 0 0;
  background: linear-gradient(
    247.11deg,
    #e3e7f6 3.56%,
    rgba(227, 231, 246, 0) 115.17%
  );
  position: relative;
  height: 730px;
  @include small-tablet {
    height: auto;
    padding: 140px 0 50px 0;
  }
  // .dreamers__container

  &__container {
    display: flex;
    align-items: center;
    @include small-tablet {
      flex-direction: column;
    }
  }

  // .dreamers__content

  &__content {
    max-width: 598px;
  }

  // .dreamers__title

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 48px;
    line-height: 62px;
    color: var(--blue);
    margin-bottom: 40px;
    @include mobile {
      font-size: 30px;
    }
  }

  // .dreamers__img

  &__img {
    position: absolute;
    bottom: 0;
    right: 0;
    @include small-tablet {
      position: static;
      margin-top: 40px;
    }
  }
}
