.code {
  padding: 65px 0;
  background: linear-gradient(
    268.52deg,
    #e3e7f6 -52.27%,
    rgba(227, 231, 246, 0) 138.23%
  );
  // .code__container

  &__container {
  }

  // .code__content

  &__content {
    text-align: center;
  }

  // .code__title

  &__title {
    margin-bottom: 16px;
  }

  // .code__row

  &__row {
    margin-top: 95px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 37px;
  }

  // .code__col

  &__col {
    flex: 1 0 30%;
    width: 345px;
    background: #ffffff;
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07),
      0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
    padding: 40px;
  }

  // .code__col-title

  &__col-title {
    margin: 0;
    font-weight: 400;
    font-size: 28px;
    line-height: 31px;
    color: var(--dark);
    margin-bottom: 32px;
  }

  // .code__col-text

  &__col-text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--grayish-blue);
  }
}
