.offer {
  padding: 120px 0 50px 0;
  @include tablet {
    padding: 40px 0;
  }
  // .offer__title

  &__title {
    text-align: center;
    margin-bottom: 16px;
  }
  // .offer__text

  &__text {
    text-align: center;
    margin-bottom: 16px;
  }
  // .offer__row

  &__row {
    margin-top: 100px;
    display: grid;
    gap: 35px;
    grid-template-columns: repeat(12, 1fr);
    @include mobile {
      display: block;
    }
  }

  // .offer__col

  &__col {
    grid-column: 4 span;
    background: #ffffff;
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07),
      0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
    padding: 46px 40px 40px 40px;
    @include tablet {
      grid-column: 6 span;
    }
    @include mobile {
      &:not(:last-child) {
        margin-bottom: 36px;
      }
    }
  }

  // .offer__col-title

  &__col-title {
    margin: 0;
    font-weight: 400;
    font-size: 28px;
    line-height: 31px;
    color: var(--dark);
    margin-bottom: 38px;
  }

  // .offer__col-text

  &__col-text {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 27px;
    color: var(--grayish-blue);
  }
  // .offer-icon-1
  &-icon-1 {
    display: flex;
    align-items: center;
    &::before {
      content: url(../img/career/col-icon-1.svg);
      margin-right: 10px;
    }
  }
  // .offer-icon-2
