.about {
  padding: 175px 0 10px 0;
  background: linear-gradient(
    247.11deg,
    #e3e7f6 3.56%,
    rgba(227, 231, 246, 0) 115.17%
  );
  // .about__container

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include small-tablet {
      justify-content: center;
      flex-direction: column;
    }
  }

  // .about__content

  &__content {
    max-width: 390px;
    @include small-tablet {
      max-width: 580px;
      text-align: center;
      margin-bottom: 40px;
    }
  }

  // .about__title

  &__title {
    margin-bottom: 32px;
  }

  // .about__text

  &__text {
    font-size: 20px;
  }
}
