.life {
  padding: 50px 0;
  background: linear-gradient(
    270.76deg,
    #e3e7f6 -55.5%,
    rgba(227, 231, 246, 0) 145.36%
  );
  // .life__container

  &__container {
    display: flex;
    align-items: flex-end;
    gap: 40px;
    justify-content: space-between;
    @include mobile {
      flex-direction: column;
    }
  }

  // .life__left

  &__left {
    display: flex;
    flex-direction: column;
  }

  // .life__descr

  &__descr {
    max-width: 323px;
    margin-bottom: 100px;
  }

  // .life__title

  &__title {
    max-width: 110px;
  }

  // .life__photo

  &__photo {
    &:not(:last-child) {
      margin-bottom: 45px;
    }
    img {
      border-radius: 8px;
    }
  }

  // .life__right

  &__right {
    display: flex;
    flex-direction: column;
  }
}
