.cookieyes {
  padding: 102px 0;
  @include small-tablet {
    padding: 40px 0;
  }
  // .cookieyes__container

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include small-tablet {
      flex-direction: column;
      justify-content: center;
    }
  }

  // .cookieyes__descr

  &__descr {
    max-width: 400px;
    @include small-tablet {
      text-align: center;
      max-width: 700px;
      margin-bottom: 40px;
    }
  }

  // .cookieyes__img

  &__img {
    margin-bottom: 40px;
  }

  // .cookieyes__text

  &__text:not(:last-child) {
    margin-bottom: 10px;
  }
}
