.recommend {
  padding: 50px 0;
  // .recommend__content

  &__content {
    background: #f7f8ff;
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07),
      0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
    padding: 40px 60px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @include tablet {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
    @include small-tablet {
      padding: 20px;
    }
  }

  // .recommend__percent

  &__percent {
    display: flex;
    align-items: center;
    @include small-tablet {
      flex-direction: column;
      margin-bottom: 20px;
    }
  }

  // .recommend__percent-text

  &__percent-text {
    margin-left: 25px;
    max-width: 330px;
    font-weight: 700;
    font-size: 36px;
    line-height: 48px;
    color: var(--blue);
    @include small-tablet {
      max-width: none;
      margin-left: 0;
      font-size: 25px;
    }
  }

  // .recommend__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  // .recommend__info-logo

  &__info-logo {
    margin-bottom: 5px;
  }

  // .recommend__info-rating

  &__info-rating {
    display: flex;
    align-items: center;
    margin-bottom: 23px;
  }

  // .recommend__info-star__item

  &__info-star__item {
    font-weight: 700;
    font-size: 36px;
    line-height: 62px;
    color: var(--black);
    margin-right: 5px;
  }
  // .recommend__info-link
  &__info-link {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    text-decoration-line: underline;
    color: var(--dark-blue);
    &::after {
      content: url(../img/career/arrow.svg);
      width: 20px;
      height: 20px;
      margin-left: 4px;
    }
  }
}
