.work {
  padding: 50px 0;

  // .work__title

  &__title {
    text-align: center;
    margin-bottom: 16px;
  }

  // .work__text

  &__text {
    text-align: center;
  }

  // .work__content

  &__content {
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  // .work__item

  &__item {
    background: #ffffff;
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07),
      0px 17px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
    padding: 35px 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    @include small-tablet {
      flex-direction: column;
      padding: 15px;
    }
  }

  //word__descr

  &__descr {
    @include small-tablet {
      margin-bottom: 30px;
    }
  }

  // .work__descr-pos

  &__descr-pos {
    font-weight: 400;
    font-size: 28px;
    line-height: 27px;
    margin-bottom: 24px;
    color: var(--grayish-blue);
    @include mobile {
      text-align: center;
      font-size: 22px;
    }
  }

  // .work__descr-info

  &__descr-info {
    display: flex;
    gap: 30px;
    @include small-tablet {
      text-align: center;
      align-items: center;
      justify-content: center;
    }
  }

  // .work__descr-time

  &__descr-time {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: var(--grayish);
    &::before {
      content: url(../img/career/time.svg);
      width: 22px;
      height: 22px;
      margin-right: 5px;
    }
  }

  // .work__descr-location

  &__descr-location {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: var(--grayish);
    &::before {
      content: url(../img/career/map.svg);
      width: 22px;
      height: 22px;
      margin-right: 5px;
    }
  }

  //.work__more

  &__more {
    margin: 0 auto;
    padding: 14px 32px;
    background: #bf0830;
    border-radius: 6px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--white);
  }
}
