.logo {
  z-index: 50;
  @include mobile {
    width: 90px;
  }
}

.main-title {
  margin: 0;
  font-weight: 700;
  font-size: 44px;
  line-height: 62px;
  color: var(--blue);
  @include mobile {
    font-size: 27px;
    line-height: 45px;
  }
}
.main-text {
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 33px;
  color: var(--grayish-blue);
  @include mobile {
    line-height: 27px;
  }
}

.main-btn {
  margin: 0;
  padding: 14px 32px;
  border-radius: 6px;
  background: transparent;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.005em;
  color: var(--strong-red);
  border: 1px solid var(--strong-red);
  transition: 0.3s ease-in-out;
  &:hover {
    color: var(--white);
    background: var(--strong-red);
    transition: 0.3s ease-in-out;
  }
  @include mobile {
    padding: 15px;
    font-size: 14px;
  }
}
.hero-page {
  @include flex-between;
}
.red {
  color: var(--strong-red);
}
