.future {
  padding: 136px 0;
  background: linear-gradient(
    342.12deg,
    #e3e7f6 -6.53%,
    rgba(227, 231, 246, 0) 87.81%
  );
  @include small-tablet {
    padding: 50px 0;
  }
  // .future__container

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  // .future__content

  &__content {
    text-align: center;
    max-width: 800px;
  }

  // .future__title

  &__title {
    margin-bottom: 20px;
  }

  // .future__text

  &__text {
    margin-bottom: 40px;
  }

  // .future__btn

  &__btn {
    display: flex;
    justify-content: center;
    gap: 20px;
  }
}
