.products {
  padding: 100px 0;
  // .producst__title

  &__title {
    text-align: center;
    margin-bottom: 16px;
  }

  // .producsts__text

  &__text {
    text-align: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
  }

  // .products__content

  &__content {
    margin-top: 90px;
    display: flex;
    justify-content: center;
    gap: 32px;
    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }
  // .products__info

  &__info {
    background: #ffffff;
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07),
      0px 25px 35px rgba(22, 23, 72, 0.08);
    border-radius: 8px;
    padding: 66px 20px 0 10px;
    max-width: 538px;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  // .products__wrapper
  &__wrapper {
    max-width: 352px;
    padding-left: 52px;
    @include mobile {
      padding-left: 0;
    }
  }
  // .products__logo

  &__logo {
    margin-bottom: 45px;
  }
  // .products__descr

  &__descr {
    margin: 0;
    max-width: 352px;
    font-weight: 400;
    font-size: 24px;
    line-height: 31px;
    color: var(--dark);
    margin-bottom: 30px;
    @include mobile {
      font-size: 21px;
    }
  }

  // .products__btn

  &__btn {
    border: 1px solid var(--strong-red);
    border-radius: 6px;
    padding: 12px 24px;
    background: transparent;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.005em;
    color: var(--strong-red);
    &::before {
      content: url(../img/home/row.svg);
      position: relative;
      top: 2px;
      margin-right: 9px;
    }
    @include mobile {
      margin-bottom: 10px;
    }
  }

  // .products__img
  &__img {
    margin-bottom: -3px;
  }
}
