.nav {
  // .nav__list
  @include flex-v-center;
  &__list {
    display: flex;
  }

  // .nav__item

  &__item:not(:first-child) {
    margin-left: 60px;
  }

  // .nav__link

  &__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: var(--gray-blue);
    &:hover {
      color: var(--strong-red);
      transition: 0.3s ease-in-out;
    }
  }

  // .nav__btn

  &__btn {
    margin-left: 30px;
  }
}
.burger {
  display: none;
  @include burger;
  z-index: 50;
}
@include small-tablet {
  .burger {
    display: block;
  }
  .nav__list {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    transform: translateX(100%);
    transition: 0.5s ease;
    background-color: var(--white);
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .nav__btn {
    margin-left: 0;
    z-index: 50;
  }
  .nav__item:not(:first-child) {
    margin-left: 0px;
  }
  .nav__item {
    margin-bottom: 50px;
  }
  .menu--active {
    transform: translateX(0);
    transition: 0.5s ease;
  }
}
