.connect {
  padding: 220px 0 0px 0;
  background: linear-gradient(
    247.11deg,
    #e3e7f6 3.56%,
    rgba(227, 231, 246, 0) 115.17%
  );
  @include small-tablet {
    padding: 120px 0 0 0;
  }
  // .connect__container

  &__container {
    display: flex;
    justify-content: space-between;
    @include tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  // .connect__content

  &__content {
    max-width: 440px;
    @include tablet {
      text-align: center;
    }
  }

  // .connect__title

  &__title {
    margin-bottom: 40px;
  }

  // .connect__text

  &__text {
    margin-bottom: 40px;
  }

  // .connect__btn

  &__btn {
    margin-bottom: 40px;
  }
}
