.build {
  padding: 110px 0;
  background: linear-gradient(
    335.73deg,
    #e3e7f6 12.46%,
    rgba(227, 231, 246, 0) 84.19%
  );
  @include small-tablet {
    padding: 50px 0;
  }
  // .build__container

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include small-tablet {
      flex-direction: column;
      justify-content: center;
    }
  }

  // .build__content

  &__content {
    max-width: 343px;
    @include small-tablet {
      text-align: center;
      max-width: 430px;
    }
  }

  // .build__title

  &__title {
    margin-bottom: 16px;
  }

  // .build__text

  &__text {
    margin-bottom: 16px;
  }

  // .build__img

  &__img {
    @include small-tablet {
      margin-top: 40px;
    }
  }
}
