// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Open Sans", sans-serif;
  --content-width: 1078px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white: #fff;
  --gray-blue: #646a89;
  --grayish-blue: #484c63;
  --blue: #1a2578;
  --strong-red: #bf0830;
  --dark: #3a3a3c;
  --black: #000;
  --dark-blue: #0d123c;
  --grayish: #8f90a6;
}
