.contacts {
  padding: 100px 0;
  // .contacts__container

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 15px;
    @include small-tablet {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  // .contacts__info

  &__info {
    display: flex;
    flex-direction: column;
    max-width: 300px;
    gap: 70px;
    @include small-tablet {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }

  // .contacts__loc

  &__loc {
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    color: var(--black);
    margin-bottom: 25px;
  }

  // .contacts__descr

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--black);
    max-width: 265px;
  }
  &__icon-1 {
    display: flex;
    align-items: center;
    &::before {
      content: url(../img/contact/icon-1.svg);
      display: inline-block;
      width: 40px;
      height: 27px;
      margin-right: 20px;
    }
  }
  &__icon-2 {
    display: flex;
    align-items: center;
    &::before {
      content: url(../img/contact/icon-2.svg);
      display: inline-block;
      width: 40px;
      height: 27px;
      margin-right: 20px;
    }
  }
  &__icon-3 {
    display: flex;
    align-items: center;
    &::before {
      content: url(../img/contact/icon-2.svg);
      display: inline-block;
      width: 40px;
      height: 27px;
      margin-right: 20px;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 30px;
  background: #ffffff;
  box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07),
    0px 25px 35px rgba(22, 23, 72, 0.05);
  border-radius: 8px;
  padding: 55px 45px;
  max-width: 540px;
  width: 100%;
  // .form__label

  &__label {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  // .form__name

  &__name {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.005em;
    color: var(--grayish);
    margin-bottom: 7px;
  }

  // .form__input

  &__input {
    width: 100%;
    padding: 20px 32px;
    background: #ffffff;
    border: 1px solid #e3e7f6;
    border-radius: 6px;
  }

  //form__textarea

  &__textarea {
    background: #ffffff;
    border: 1px solid #e3e7f6;
    border-radius: 6px;
    padding: 20px 32px;
    resize: none;
  }

  // .form__btn

  &__btn {
    padding: 14px 32px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--white);
    background: #bf0830;
    border-radius: 6px;
  }
}
