.looking {
  padding: 100px 0;
  background: linear-gradient(
    342.12deg,
    #e3e7f6 -6.53%,
    rgba(227, 231, 246, 0) 87.81%
  );
  // .looking__container

  &__container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  // .looking__title

  &__title {
    text-align: center;
    margin-bottom: 16px;
  }

  // .looking__text

  &__text {
    text-align: center;
    margin-bottom: 40px;
  }
}
