.hero {
  padding-top: 230px;
  height: 900px;
  @include bg-cover;
  background: linear-gradient(252.91deg, #e3e7f6 3.68%, #e3e7f600 97.82%);
  position: relative;
  @media (max-width: 1250px) {
    padding-bottom: 50px;
    height: auto;
  }
  @include mobile {
    padding-top: 100px;
  }
  // .hero__container

  .hero__container {
    @media (max-width: 1250px) {
      flex-direction: column;
      align-items: center;
    }
  }

  // .hero__content

  &__content {
    max-width: 570px;
    @media (max-width: 1250px) {
      max-width: 800px;
    }
  }

  // .hero__title

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 48px;
    line-height: 62px;
    color: var(--blue);
    margin-bottom: 32px;
    @include mobile {
      font-size: 26px;
      line-height: 50px;
    }
  }

  // .hero__text

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 22px;
    line-height: 33px;
    color: var(--grayish-blue);
    margin-bottom: 40px;
    @include mobile {
      font-size: 18px;
    }
  }

  // .hero__btn

  &__btn {
    display: flex;
  }

  // .hero__btn-item

  &__btn-item:not(:first-child) {
    margin-left: 16px;
  }

  // .hero__img

  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    @media (max-width: 1250px) {
      position: static;
      margin-top: 50px;
    }
  }
}
