.header {
  padding: 25px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background: rgb(255, 255, 255, 0.7);
  @include mobile {
    padding: 15px 0;
  }
  // .header__container

  &__container {
    @include flex-all-sb;
  }
}
.header-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  padding: 20px;
  transition: 0.5s;
}
