.footer {
  padding: 65px 0;
  background: var(--dark-blue);
  // .footer__top

  &__top {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 40px;
    padding-bottom: 64px;
    margin-bottom: 64px;
    border-bottom: 1px solid rgba(197, 203, 226, 0.5);
  }

  // .footer__info

  &__info {
    max-width: 300px;
  }

  // .footer__descr

  &__descr {
    margin: 0;
    margin-top: 25px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--white);
  }

  // .footer__nav

  &__nav {
    display: flex;
    flex-wrap: wrap;
    column-gap: 105px;
  }

  // .footer__nav-title

  &__nav-title {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: var(--white);
  }

  // .footer__nav-menu

  &__nav-menu {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  // .footer__nav-link

  &__nav-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: var(--white);
    transition: 0.5s;
    &:hover {
      transition: 0.5s;
      color: var(--strong-red);
    }
  }

  // .footer__bottom

  &__bottom {
    display: flex;
    justify-content: space-between;
    @include mobile {
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }
  }

  // .footer__bottom-text

  &__bottom-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: var(--white);
  }
}
.social {
  // .social__item

  &__item {
    margin-top: 30px;
    display: flex;
    gap: 15px;
  }

  // .social__link

  &__link {
    svg {
      fill: var(--white);
      width: 20px;
      height: 20px;
      transition: 0.5s;
      &:hover {
        transition: 0.5s;
        fill: var(--strong-red);
      }
    }
  }
}
