.innovation {
  padding: 120px 0 30px 0;
  background: #e5e5e5;
  @include mobile {
    padding: 50px 0;
  }

  // .innovation__ttitle

  &__title {
    text-align: center;
  }

  // .innovation__text

  &__text {
    text-align: center;
  }

  // .innovation__content
  &__content {
    margin-top: 100px;
  }

  // .innovation__slider

  &__slider {
    position: relative;

    &-slide {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 119px;
      background: #ffffff;
      box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07),
        0px 25px 35px rgba(22, 23, 72, 0.05);
      border-radius: 8px;
      picture {
        padding: 10px 40px;
      }
    }
  }

  // .innovation__nav

  &__nav {
    margin-top: 50px;
    display: flex;
    gap: 8px;
    // .innovation__nav-prev
    &-prev {
      cursor: pointer;
      svg {
        width: 24px;
        height: 24px;
        fill: #c2d1d9;
        transition: 0.5s;
        &:hover {
          fill: #424b5a;
          transition: 0.5s;
        }
      }
    }
    // .innovation__nav-next
    &-next {
      cursor: pointer;
      svg {
        width: 24px;
        height: 24px;
        fill: #c2d1d9;
        transition: 0.5s;
        &:hover {
          fill: #424b5a;
          transition: 0.5s;
        }
      }
    }
  }
}
