.creation {
  padding: 50px 0;
  // .creation__container

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row-reverse;
    @include small-tablet {
      flex-direction: column;
      justify-content: center;
    }
  }

  // .creation__content

  &__content {
    max-width: 400px;
    @include small-tablet {
      text-align: center;
    }
  }

  // .creation__title

  &__title {
    margin-bottom: 16px;
  }

  // .creation__text

  &__text {
    margin-bottom: 20px;
  }

  // .creation__img

  &__img {
    margin-top: 40px;
  }
}
