.information {
  padding: 50px 0;

  background: linear-gradient(
    268.52deg,
    #e3e7f6 -52.27%,
    rgba(227, 231, 246, 0) 138.23%
  );
  // .information__container

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include small-tablet {
      flex-direction: column;
      justify-content: center;
    }
  }

  // .information__content

  &__content {
    max-width: 400px;
    @include small-tablet {
      text-align: center;
    }
  }

  // .information__title

  &__title {
    margin-bottom: 16px;
  }

  // .information__text

  &__text {
    margin-bottom: 20px;
  }

  // .information__img

  &__img {
    margin-top: 40px;
  }
}
