.trust {
  padding: 80px 0 120px 0;
  border-bottom: 1px solid rgba(197, 203, 226, 0.5);
  @include mobile {
    padding: 50px;
  }

  // .trust__title

  &__title {
    margin: 0;
    text-align: center;
    font-weight: 700;
    font-size: 36px;
    line-height: 57px;
    color: var(--blue);
    @include mobile {
      font-size: 22px;
      line-height: 43px;
    }
  }

  // .trust__row

  &__row {
    margin-top: 85px;
    display: flex;
    gap: 40px;
    justify-content: center;
    align-items: center;
    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  // .trust__users

  &__users {
    max-width: 115px;
    text-align: center;
    @include small-tablet {
      flex: 1 1 35%;
    }
  }
}
