.creators {
  padding: 70px 0;
  @include bg-cover;
  background: linear-gradient(
    268.52deg,
    #e3e7f6 -52.27%,
    rgba(227, 231, 246, 0) 138.23%
  );
  // .creators__container

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include small-tablet {
      flex-direction: column;
      justify-content: center;
    }
  }

  // .creators__content

  &__content {
    max-width: 416px;
    @include small-tablet {
      margin-bottom: 40px;
    }
  }

  // .creators__title

  &__title {
    margin-bottom: 16px;
  }
}
